<template>
  <div>
    <q-img
      class="q-ma-lg"
      :src="require('../../assets/logo_l.png')"
      alt="image"
      style="width: 100%; max-width: 300px"
    />
    <div class="q-mt-lg row q-gutter-md justify-center items-center">
      <div id="user-video" class="video-container"></div>
      <div>
        <q-img
          :src="require('../../assets/profile/' + imageNo + '.png')"
          width="30%"
        />
        <q-btn
          color="primary"
          @click="alert = true"
          icon="arrow_drop_down"
          class="q-ma-sm"
          flat
          round
        />
        <div>
          <q-dialog v-model="alert">
            <q-card
              style="background-color: #b3a286; color: white; width: 200px"
            >
              <q-card-section>
                <table width="100%" style="text-align: center">
                  <tr>
                    <td width="10%"></td>
                    <td width="70%" class="q-px-ml">
                      {{ setting.ageMin }} - {{ setting.ageMax }}
                    </td>
                  </tr>
                  <q-separator inset />
                  <tr>
                    <td></td>
                    <td>{{ filtered_distance }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      {{ filtered_drink }}
                    </td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{{ filtered_smoke }}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{{ filtered_religion }}</td>
                  </tr>
                </table>
              </q-card-section>

              <q-card-actions align="center">
                <q-btn flat label="OK" color="primary" v-close-popup />
              </q-card-actions>
            </q-card>
          </q-dialog>
        </div>
        <br /><br />
        <q-btn-dropdown
          split
          color="primary"
          @click="toggleAudio()"
          @show="getAudioDevices()"
        >
          <template v-slot:label>
            <div>
              <template v-if="isOn.audio">
                <div class="row items-center no-wrap" style="width: 100px">
                  <q-icon left name="mic" />
                  <div class="text-center"> </div>
                </div>
              </template>
              <template v-else>
                <div class="row items-center no-wrap" style="width: 100px">
                  <q-icon left name="mic_off" />
                  <div class="text-center"> </div>
                </div>
              </template>
            </div>
          </template>
          <q-list>
            <q-item
              v-for="(device, index) in audioDevices"
              :key="index"
              clickable
              v-close-popup
              @click="updateAudio(index)"
            >
              <q-item-section>
                <q-item-label>{{ device.label }}</q-item-label>
              </q-item-section>
            </q-item>
          </q-list>
        </q-btn-dropdown>
        <br /><br />

        <br />
        <q-btn-dropdown
          split
          color="primary"
          @click="toggleVideo()"
          @show="getVideoDevices()"
        >
          <template v-slot:label>
            <div>
              <template v-if="isOn.video">
                <div class="row items-center no-wrap" style="width: 100px">
                  <q-icon left name="videocam" />
                  <div class="text-center"> </div>
                </div>
              </template>
              <template v-else>
                <div class="row items-center no-wrap" style="width: 100px">
                  <q-icon left name="videocam_off" />
                  <div class="text-center"> </div>
                </div>
              </template>
            </div>
          </template>
          <q-list>
            <q-item
              v-for="(device, index) in videoDevices"
              :key="index"
              clickable
              v-close-popup
              @click="updateVideo(index)"
            >
              <q-item-section>
                <q-item-label>{{ device.label }}</q-item-label>
              </q-item-section>
            </q-item>
          </q-list>
        </q-btn-dropdown>
      </div>
    </div>
    <br />
    <template v-if="!isMatching">
      <div class="q-gutter-md">
        <q-btn label=" " color="primary" @click="startMatch()" />
        <q-btn
          label=""
          color="secondary"
          @click="this.$router.push('/')"
        />
      </div>
    </template>
    <template v-else>
      <div class="q-gutter-md">
        <q-btn label=" " color="secondary" @click="cancelMatch()" />
        <q-btn label="" color="secondary" @click="backToMain()" />
      </div>
    </template>
    <MatchModal
      v-model="this.showChoiceModal"
      @confirm="confirm"
      @close="close"
      @changeDisable="this.disable = true"
      :imageNo="opponentInfo.imageNo + ''"
      :modalContent="this.modalContent"
      :disable="this.disable"
      @hide="this.disable = false"
    />
    <ConfirmModal
      v-model="this.showModal"
      :modalContent="this.confirlModalContent"
    />
  </div>
</template>

<script>
import { OpenVidu } from 'openvidu-browser'
import { onMounted, onUnmounted } from 'vue'
import {
  getMeetingSetting,
  enterMeeting,
  startMatching,
  checkMatching,
  successMatching,
  stopMatching
} from '@/api/meeting.js'
import jwtDecode from 'jwt-decode'
import { mapState, mapActions } from 'vuex'
import { WEBSOCKET_URL } from '@/config'
import SockJS from 'sockjs-client'
import Stomp from 'stompjs'
import MatchModal from './MatchModal.vue'
import ConfirmModal from '../ConfirmModal.vue'

const meetingStore = 'meetingStore'

// stompClient.debug = null // disable stomp loggings

export default {
  name: 'App',

  components: { MatchModal, ConfirmModal },

  data() {
    onMounted(() => {
      this.imageNo = this.myProfile().imageNo
      //   
      getMeetingSetting(
        (res) => {
          this.setting = res.data.meetingSetting
        },
        (error) => console.warn(error)
      )
      //   
      navigator.geolocation.getCurrentPosition((loc) => {
        this.location.lat = loc.coords.latitude
        this.location.lng = loc.coords.longitude
      })
      //   
      this.joinSession()
    })
    onUnmounted(() => {
      //  clearInterval .
      clearInterval(this.interval)
      this.leaveSession()
    })

    return {
      interval: null,

      socket: null,
      stompClient: null,

      location: { lat: 0, lng: 0 },

      disable: false,
      imageNo: 1,
      myInfo: {},
      opponentInfo: {},

      myGender: null,
      myNo: null,
      opponentNo: null,
      // modal ~
      newSessionId: null,
      showChoiceModal: false,
      modalContent: null,
      mAccepted: 0,
      fAccepted: 0,
      // ~ modal

      // confirm modal ~
      showModal: false,
      confirlModalContent: ' .',
      // ~ confirm modal

      OV: undefined,
      session: undefined,
      mainStreamManager: undefined,

      publisher: undefined,
      videoDevices: [],
      audioDevices: [],
      audioId: undefined,
      videoId: undefined,

      subscribers: undefined,
      isOn: {
        audio: true,
        video: true
      },

      setting: null,

      mySessionId: new Date().toISOString() + Math.floor(Math.random() * 10000), //    + 4  
      myUserName: 'Participant' + Math.floor(Math.random() * 100), //   

      alert: false,

      isMatching: false
    }
  },
  computed: {
    ...mapState(meetingStore, ['myPublisher', 'sessionId']),

    filtered_distance() {
      if (this.setting.distance === 0) return '10km'
      else if (this.setting.distance === 1) return '20km'
      else if (this.setting.distance === 2) return '50km'
      else if (this.setting.distance === 3) return '100km'
      else if (this.setting.distance === 4) return 'anywhere'
      else return 'unknown'
    },
    filtered_drink() {
      let a = ''
      let b = ''
      if (this.setting.drinkMin === 0) a = ''
      else if (this.setting.drinkMin === 1) a = ''
      else if (this.setting.drinkMin === 2) a = ''
      else a = 'unknown'
      if (this.setting.drinkMax === 0) b = ''
      else if (this.setting.drinkMax === 1) b = ''
      else if (this.setting.drinkMax === 2) b = ''
      else b = 'unknown'
      return a + ' ~ ' + b
    },
    filtered_smoke() {
      if (this.setting.smoke === 0) return ''
      else if (this.setting.smoke === 1) return ''
      else if (this.setting.smoke === 2) return ''
      else return 'unknown'
    },
    filtered_religion() {
      if (this.setting.religion === 0) return ''
      else if (this.setting.religion === 1) return ''
      else return 'unknown'
    }
  },
  methods: {
    ...mapActions(meetingStore, [
      'setSessionId',
      'setPublisher',
      'setOpponentInfo',
      'setIsMatched'
    ]),

    intervalMatching() {
      return setInterval(
        checkMatching,
        5000,
        (res) => {
          if (res.data.user !== null) {
            this.opponentInfo = res.data.user
            this.modalContent = res.data.user.nickname + ' .'
            this.showChoiceModal = true
            this.opponentInfo = { ...res.data.user } //    
            clearInterval(this.interval)

            this.newSessionId =
              this.myProfile().NickName + new Date().toISOString()
            this.setSessionId(this.newSessionId)
            this.subscribeSession()
            // 11

            this.stompClient.send(
              '/pub/match',
              {},
              JSON.stringify({
                roomId: this.newSessionId,
                maleNo: res.data.user.no,
                female: this.myInfo
              })
            )
          }
        },
        () => console.warn(' .')
      )
    },
    startMatch() {
      this.isMatching = true
      //   (  )
      this.commonSubscribe('/sub/match')

      //  5   API 
      // (    message )
      if (this.myProfile().gender === 'F') {
        this.interval = this.intervalMatching()
      }
    },
    cancelMatch() {
      stopMatching()
      this.isMatching = false
      clearInterval(this.interval)
      this.stompClient.disconnect()
    },
    backToMain() {
      if (this.isMatching) stopMatching()
      this.stompClient.disconnect()
      this.$router.push('/')
    },

    //    
    commonSubscribe(url) {
      this.socket = new SockJS(WEBSOCKET_URL) // config !!!!!!!!
      this.stompClient = Stomp.over(this.socket)
      this.stompClient.connect({}, (frame) => {
        startMatching(
          this.location,
          (res) => {
            this.myInfo = res.data.user
          },
          (err) => console.warn(err)
        )
        this.stompClient.subscribe(url, (msg) => {
          const response = JSON.parse(msg.body)
          if (
            this.myGender === 'M' &&
            response.maleNo === parseInt(this.myInfo.no)
          ) {
            this.opponentInfo = response.female
            this.modalContent =
              response.female.nickname + ' .'
            this.showChoiceModal = true
            this.setSessionId(response.roomId)
            this.subscribeSession()
            // 11
          }
        })
      })
    },

    // modal ~
    //    (setTimeout 11   )
    subscribeSession() {
      stopMatching()
      this.stompClient.subscribe('/sub/chat/' + this.sessionId, (msg) => {
        const res = JSON.parse(msg.body)
        if (parseInt(res.nickname) === this.myNo) {
          if (this.myGender === 'M') this.mAccepted = res.content === 'true'
          else this.fAccepted = res.content === 'true'
        } else {
          if (this.myGender === 'M') this.fAccepted = res.content === 'true'
          else this.mAccepted = res.content === 'true'
          this.opponentNo = parseInt(res.nickname)
        }
        if (this.mAccepted && this.fAccepted) {
          //    
          clearTimeout(timeout)
          this.setIsMatched(true)
          if (this.myGender === 'M') {
            successMatching(
              this.opponentNo,
              (res) => {
                this.opponentInfo = res.data.user
              },
              (error) => {
                console.warn(error)
              }
            ) //   API  
          }
          // vuex   
          this.setOpponentInfo(this.opponentInfo)
          this.sendSetting()

          this.$router.push('/meeting/room')
        }
        //    1
        if (this.mAccepted === false && this.fAccepted === false) {
          clearTimeout(timeout)
          this.showChoiceModal = false
          console.warn(' .')
          if (this.myGender === 'F') this.interval = this.intervalMatching()
          this.mAccepted = null
          this.fAccepted = null
          startMatching(this.location)
          this.showModal = true
          setTimeout(() => (this.showModal = false), 3000)
        }
      })
      const timeout = setTimeout(() => {
        this.showChoiceModal = false
        //    2
        console.warn(' .')
        startMatching(this.location)
        if (this.myGender === 'F') this.interval = this.intervalMatching()
        this.mAccepted = null
        this.fAccepted = null
        this.showModal = true
        setTimeout(() => (this.showModal = false), 3000)
      }, 11000)
    },

    confirm() {
      const request = { nickname: this.myNo, content: true }
      this.stompClient.send(
        '/pub/chat/' + this.sessionId,
        {},
        JSON.stringify(request)
      )
    },

    close() {
      const request = { nickname: this.myNo, content: false }
      this.stompClient.send(
        '/pub/chat/' + this.sessionId,
        {},
        JSON.stringify(request)
      )
    },

    // ~ modal

    myProfile() {
      const token = sessionStorage.getItem('Authorization')
      const profile = jwtDecode(token)
      this.myGender = profile.gender
      this.myNo = parseInt(profile.sub)
      return profile
    },

    //     
    getVideoDevices() {
      this.OV.getDevices().then(
        (devices) =>
          (this.videoDevices = devices.filter(
            (device) => device.kind === 'videoinput'
          ))
      )
    },
    getAudioDevices() {
      this.OV.getDevices().then(
        (devices) =>
          (this.audioDevices = devices.filter(
            (device) => device.kind === 'audioinput'
          ))
      )
    },

    //    
    updateAudio(index) {
      this.OV.getDevices().then((devices) => {
        const audioDevices = devices.filter(
          (device) => device.kind === 'audioinput'
        )
        if (audioDevices && audioDevices.length > 1) {
          const newPublisher = this.OV.initPublisher('user-video', {
            audioSource: audioDevices[index].deviceId,
            videoSource: this.videoId,
            publishAudio: this.isOn.audio,
            publishVideo: this.isOn.video,
            resolution: '500x375', // The resolution of your video
            mirror: false
          })
          this.session.unpublish(this.publisher).then(() => {
            this.publisher = newPublisher
            this.session.publish(this.publisher)
          })
          this.audioId = audioDevices[index].deviceId
        }
      })
    },
    updateVideo(index) {
      this.OV.getDevices().then((devices) => {
        const videoDevices = devices.filter(
          (device) => device.kind === 'videoinput'
        )
        if (videoDevices && videoDevices.length > 1) {
          const newPublisher = this.OV.initPublisher('user-video', {
            audioSource: this.audioId,
            videoSource: videoDevices[index].deviceId,
            publishAudio: this.isOn.audio,
            publishVideo: this.isOn.video,
            resolution: '500x375', // The resolution of your video
            mirror: false
          })
          this.session.unpublish(this.publisher).then(() => {
            this.publisher = newPublisher
            this.session.publish(this.publisher)
          })
          this.videoId = videoDevices[index].deviceId
        }
      })
    },

    //   
    toggleAudio() {
      this.isOn.audio = !this.isOn.audio
      this.publisher.publishAudio(this.isOn.audio)
    },
    toggleVideo() {
      this.isOn.video = !this.isOn.video
      this.publisher.publishVideo(this.isOn.video)
    },

    //           vuex  
    sendSetting() {
      this.setPublisher({
        audioSource: this.audioId,
        videoSource: this.videoId,
        publishAudio: this.isOn.audio,
        publishVideo: this.isOn.video
      })
    },

    //     
    joinSession() {
      this.OV = new OpenVidu()
      this.OV.enableProdMode() // @ disable all logging except error level
      this.session = this.OV.initSession()
      this.session.on('streamCreated', ({ stream }) => {
        const subscriber = this.session.subscribe(stream)
        this.subscribers = [subscriber]
      })
      this.session.on('streamDestroyed', () => {
        this.subscribers = []
      })
      this.session.on('exception', ({ exception }) => {
        console.warn(exception)
      })

      this.getToken(this.mySessionId).then((response) => {
        const token = response.data.token
        if (token === undefined) {
          console.warn('  .   .')
        }
        this.session
          .connect(token, { clientData: this.myUserName })
          .then(() => {
            // --- Get your own camera stream with the desired properties ---
            const publisher = this.OV.initPublisher('user-video', {
              ...this.myPublisher,
              audioSource: undefined,
              videoSource: undefined,
              publishAudio: true,
              publishVideo: true
            })
            this.mainStreamManager = publisher
            this.publisher = publisher
            this.session.publish(this.publisher)
          })
          .catch((error) => {
            console.warn(
              'There was an error connecting to the session:',
              error.code,
              error.message
            )
          })
      })
      window.addEventListener('beforeunload', (e) => {
        e.preventDefault()
        this.leaveSession()
        this.stompClient.disconnect()
      })
    },
    leaveSession() {
      // --- Leave the session by calling 'disconnect' method over the Session object ---
      if (this.session) this.session.disconnect()
      this.session = undefined
      this.mainStreamManager = undefined
      this.publisher = undefined
      this.subscribers = []
      this.OV = undefined
      window.removeEventListener('beforeunload', this.leaveSession)
    },
    getToken(mySessionId) {
      return this.createSession(mySessionId)
    },
    createSession(sessionId) {
      return enterMeeting(sessionId)
    }
  }
}
</script>

<style scoped>
.video-container {
  width: 500px;
  height: 375px;
  overflow: hidden;
  border-radius: 10px;
}
</style>
