<template>
  <div>
    <div class="item row items-center justify-center" style="margin-top: 70px">
      <q-img
        class="q-ma-lg"
        :src="require('../../assets/logo_manager.png')"
        alt="image"
        style="width: 100%; max-width: 300px"
      />
    </div>
    <!-- Content -->
    <!-- Login -->
    <div>
      <section class="basic-container">
        <q-form @submit="onSubmit" class="q-mt-sm row justify-center">
          <section class="q-gutter-md" style="width: 85%">
            <q-input
              label=""
              outlined
              type="email"
              v-model="loginInfo.email"
              bg-color="white"
              maxlength="30"
              required
            />
            <q-input
              label=""
              outlined
              type="password"
              v-model="loginInfo.password"
              bg-color="white"
              maxlength="20"
              required
            />
            <div>
              <q-btn
                label=""
                size="lg"
                type="submit"
                class="full-width primary"
              />
            </div>
          </section>
        </q-form>
      </section>
      <ConfirmModal
        v-model="this.showModal"
        @close="movePage"
        :modalContent="this.modalContent"
      />
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
// import { kakaoLogin } from '@/api/user'
import { managerLogin } from '@/api/manager'
import ConfirmModal from '../ConfirmModal.vue'

export default {
  setup() {
    const loginInfo = ref({ email: null, password: null })
    const showModal = ref(false)
    const willPageMove = ref(false)
    const path = ref(null)
    const modalContent = 'ID  PW  '
    return {
      loginInfo,
      showModal,
      modalContent,
      willPageMove,
      path
    }
  },
  components: {
    ConfirmModal
  },
  methods: {
    async onSubmit() {
      managerLogin(
        this.loginInfo,
        ({ data }) => {
          const token = data.authorization
          sessionStorage.setItem('Authorization', token)
          this.$router.push({ name: 'managerMain' })
        },
        ({ response }) => {
          if (response.status === 401) {
            this.modalContent = 'EMAIL PW  .'
          } else if (
            response.data &&
            response.data.message &&
            response.data.message === 'Login Prohibited'
          ) {
            this.modalContent =
              ' 5   5  .'
          } else if (
            response.data &&
            response.data.message &&
            response.data.message === 'Not Manager'
          ) {
            this.modalContent = '  .'
          } else if (response.status === 403) {
            this.modalContent = '  .'
          } else {
            this.modalContent = ' .  .'
          }
          this.showModal = true
          this.willPageMove = false
        }
      )
    },
    movePage() {
      if (this.willPageMove) {
        this.$router.push(this.path)
      }
    }
  }
}
</script>

<style scoped>
.basic-container {
  width: 400px;
}
</style>
