<template>
  <div>
    <br />
    <br />
    <h1 v-if="!emailCheck"> </h1>
    <h1 v-if="emailCheck">  </h1>
    <section class="basic-container" v-if="!emailCheck">
      <q-form class="q-gutter-md row justify-center">
        <section class="q-gutter-sm" style="width: 85%">
          <q-input
            label=""
            outlined
            bg-color="white"
            type="email"
            v-model="email"
            :readonly="isReadonly"
            :rules="[checkReadonly]"
            dense
            required
            lazy-rules="ondemand"
            hide-bottom-space
            id="email"
          >
            <template v-slot:append>
              <q-btn
                v-if="!isReadonly"
                color="secondary"
                @click="sendEmail"
                label=""
                size="10px"
                dense
              />
              <q-btn
                v-else
                color="secondary"
                @click="rewrite"
                label=""
                size="10px"
                dense
              />
            </template>
          </q-input>
          <q-input
            label=""
            outlined
            bg-color="white"
            v-model="code"
            dense
            :hint="timer"
            required
          >
            <template v-slot:append>
              <q-btn
                color="secondary"
                @click="confirm"
                label=""
                size="10px"
              />
            </template>
          </q-input>
        </section>
      </q-form>
    </section>
    <section class="basic-container" v-if="emailCheck">
      <q-form @submit="onSubmit" class="q-gutter-md row justify-center">
        <section class="q-gutter-sm" style="width: 85%">
          <q-input
            label=""
            outlined
            bg-color="white"
            type="email"
            v-model="email"
            dense
            readonly
          />

          <q-input
            label=""
            outlined
            bg-color="white"
            type="password"
            v-model="password"
            placeholder=", ,  8~16"
            :rules="[
              () =>
                /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,}$/.test(
                  password
                ) || ', ,   8~16'
            ]"
            dense
            required
          >
          </q-input>
          <q-input
            label=" "
            outlined
            bg-color="white"
            type="password"
            v-model="passwordCheck"
            dense
            :rules="[
              () => password === passwordCheck || '  '
            ]"
            lazy-rules="ondemand"
            required
          />
        </section>
        <q-btn label="" type="submit" class="primary" />
      </q-form>
    </section>
    <ConfirmModal
      v-model="this.showModal"
      @close="movePage"
      :modalContent="this.modalContent"
    />
  </div>
</template>

<script>
import { ref, reactive, computed } from 'vue'
import {
  confirmEmailCode,
  getEmail,
  setConfirmKey,
  modifyPasswordWithoutAuth
} from '@/api/user'
import ConfirmModal from '../ConfirmModal.vue'

export default {
  setup() {
    const email = ref(null)
    const code = ref(null)
    const isReadonly = ref(false)
    const emailCheck = ref(false)
    const timer = ref(null)
    const password = ref(null)
    const passwordCheck = ref(null)
    const showModal = ref(false)
    const willPageMove = ref(false)
    const path = ref(null)
    const intervalId = ref(0)
    const minutes = ref('03')
    const seconds = ref('00')
    const modalContent = 'ID  PW  '
    const pwState = reactive(
      computed(() => {
        if (password.value === null) return 0
        else if (passwordCheck.value === null) return 1
        else if (password.value === passwordCheck.value) return 3
        else return 2
      })
    )
    const pwMsg = ref([
      ' ',
      ' .',
      '  .',
      ' .'
    ])

    return {
      email,
      emailCheck,
      isReadonly,
      code,
      timer,
      password,
      passwordCheck,
      pwState,
      pwMsg,
      showModal,
      modalContent,
      willPageMove,
      path,
      intervalId,
      minutes,
      seconds
    }
  },
  components: {
    ConfirmModal
  },
  created() {
    getEmail(({ data }) => {
      if (data.statusCode === 200) {
        this.showModal = true
        this.modalContent = '  .'
        this.willPageMove = true
        this.path = '/'
      }
    })
  },
  methods: {
    sendEmail() {
      if (this.email < 3 || !this.email.includes('@')) {
        this.showModal = true
        this.modalContent = '   .'
        this.willPageMove = false
        return
      }
      setConfirmKey(
        this.email,
        ({ data }) => {
          if (data.statusCode === 200) {
            this.isReadonly = true
            this.showModal = true
            this.modalContent = ' .'
            this.willPageMove = false
            this.startTimer(60 * 3)
          }
        },
        ({ response: { data } }) => {
          this.isReadonly = true
          this.showModal = true
          this.modalContent =
            '  .  .'
          this.willPageMove = false
          clearInterval(this.intervalId)
        }
      )
    },
    async onSubmit() {
      modifyPasswordWithoutAuth(
        {
          email: this.email,
          password: this.password
        },
        ({ data }) => {
          if (data.statusCode === 200) {
            this.showModal = true
            this.modalContent = ' .'
            this.willPageMove = true
            this.path = '/user/login'
          }
        },
        ({ response }) => {
          if (response.data.message === 'Wrong Password') {
            this.modalContent =
              '  .   .'
            this.willPageMove = false
          } else if (response.status === 403) {
            this.modalContent = '  .'
            this.willPageMove = true
            this.path = '/user/signup/account'
          } else {
            this.modalContent = ' .  .'
            this.willPageMove = false
          }
          this.showModal = true
        }
      )
    },
    checkReadonly() {
      return this.emailCheck || ' '
    },
    rewrite() {
      this.isReadonly = false
      this.emailCheck = false
      clearInterval(this.intervalId)
    },
    startTimer(duration, display) {
      let remain = duration
      this.intervalId = setInterval(() => {
        this.minutes = parseInt(remain / 60, 10)
        this.seconds = parseInt(remain % 60, 10)

        this.minutes = this.minutes < 10 ? '0' + this.minutes : this.minutes
        this.seconds = this.seconds < 10 ? '0' + this.seconds : this.seconds
        this.timer = this.minutes + ':' + this.seconds
        if (--remain < 0) {
          remain = duration
          clearInterval(this.intervalId)
        }
      }, 1000)
    },
    confirm() {
      confirmEmailCode(
        {
          email: this.email,
          code: this.code
        },
        (response) => {
          if (response.data.statusCode === 200) {
            this.showModal = true
            this.modalContent = '.'
            this.emailCheck = true
            this.willPageMove = false
          }
          clearInterval(this.intervalId)
        },
        (error) => {
          if (error.response.data.message === 'Wrong Code') {
            this.modalContent = '  .'
          } else if (error.response.data.message === 'Email Confirm Time Out') {
            this.modalContent = '   .'
            clearInterval(this.intervalId)
          } else if (error.response.data.message === 'Code Not Sent') {
            this.modalContent = ' .'
          } else {
            this.modalContent = ' .  .'
          }
          this.showModal = true
          this.emailCheck = false
          this.willPageMove = false
        }
      )
    },
    movePage() {
      if (this.willPageMove) {
        this.$router.push(this.path)
      }
    }
  }
}
</script>

<style scoped>
.basic-container {
  width: 400px;
}
</style>
