<template>
  <div id="main-container" class="container">
    <!--    -->
    <div id="session">
      <h1>{{ level }}</h1>

      <div id="session-header" class="m-ma-lg row justify-around">
        <div class="column">
          <div class="video-box">
            <div id="user-video">
              <user-video :stream-manager="mainStreamManager" :level="level" />
            </div>
          </div>
          <div class="q-my-lg">
            <q-img
              :src="require('../../assets/profile/' + myImageNo + '.png')"
              width="30px"
              style="margin-right: 10px"
            />
            {{ myNickname }}
          </div>
        </div>
        <div id="video-container">
          <div class="col-5 col-sm-3 column">
            <div class="video-box">
              <user-video
                v-for="sub in subscribers"
                :key="sub.stream.connection.connectionId"
                :stream-manager="sub"
                :level="level"
              />
            </div>
            <div class="q-my-lg">
              <q-img
                :src="
                  require('../../assets/profile/' +
                    opponentInfo.imageNo +
                    '.png')
                "
                width="30px"
                style="margin-right: 10px"
              />
              {{ opponentInfo.nickname }}
            </div>
          </div>
        </div>
      </div>
      <div style="font-size: 24px">
        {{ question }}
      </div>
      <div class="q-mt-md">
        <q-btn
          @click="toggleAudio()"
          color="primary"
          v-if="isOn.audio"
          icon="mic"
          label=" "
          class="q-mr-lg"
        />
        <q-btn
          @click="toggleAudio()"
          color="primary"
          v-else
          icon="mic_off"
          label=" "
          class="q-mr-lg"
        />

        <q-btn
          @click="toggleVideo()"
          color="primary"
          v-if="isOn.video"
          icon="videocam"
          label=" "
        />

        <q-btn
          @click="toggleVideo()"
          color="primary"
          v-else
          icon="videocam_off"
          label=" "
        />
      </div>
      <div id="session-header">
        <div>
          <q-img
            id="skip_img"
            src="../../assets/meeting/skip.png"
            width="130px"
            no-spinner
            class="absolute-bottom-left"
            :class="{ disabled: skipDisable, abled: !skipDisable }"
            @click="levelUp()"
            v-if="this.level < 3"
          >
            <q-tooltip
              v-if="skipDisable"
              style="width: 200px; text-align: center"
            >
              <div>   .</div>
            </q-tooltip>
            <q-tooltip v-if="mySkip" style="width: 200px; text-align: center">
              <div>  ...</div>
            </q-tooltip>
          </q-img>

          <q-img
            src="../../assets/meeting/leave.png"
            width="130px"
            no-spinner
            @click="popupMatching = true"
            class="absolute-bottom-right abled"
          >
            <q-btn flat :ripple="false" />
          </q-img>
          <q-dialog v-model="popupMatching" persistent>
            <q-card class="popup">
              <q-bar class="popup-bar">
                <q-space />
                <q-btn color="red" dense icon="close" v-close-popup />
              </q-bar>
              <q-card-section class="popup-text text-center">
                 ?
              </q-card-section>
              <q-card-section class="q-gutter-sm text-center">
                <q-btn class="primary" label="" @click="leaveSession" />
                <q-btn
                  class="negative"
                  label=""
                  @click="popupReport = true"
                  v-close-popup
                />
                <q-btn class="secondary" label="" v-close-popup />
              </q-card-section>
            </q-card>
          </q-dialog>

          <!--    -->
          <q-dialog v-model="popupReport" persistent>
            <q-card class="report-popup" style="height: 500px">
              <q-bar class="popup-bar">
                <q-space />
                <q-btn color="red" dense icon="close" v-close-popup />
              </q-bar>
              <q-card-section class="popup-text">
                <table style="width: 100%">
                  <tr>
                    <td style="width: 20%"></td>
                    <td style="width: 40%">
                      <q-input
                        outlined
                        readonly
                        bg-color="white"
                        v-model="opponentInfo.nickname"
                        dense
                      />
                    </td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      <q-option-group
                        v-model="category"
                        :options="reportOptions"
                        type="radio"
                        color="primary"
                      />
                    </td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      <q-input
                        type="text"
                        autogrow
                        outlined
                        bg-color="white"
                        v-model="content"
                        dense
                      />
                    </td>
                  </tr>
                </table>
              </q-card-section>
              <q-card-section class="q-gutter-sm text-right">
                <q-btn
                  class="negative"
                  label=""
                  @click=";[report(), leaveSession()]"
                  type="submit"
                />
                <q-btn class="secondary" label="" v-close-popup />
              </q-card-section>
            </q-card>
          </q-dialog>
        </div>
      </div>
    </div>
    <MeetingChat />
    <ConfirmModal
      v-model="this.showModal"
      @close="this.leaveSession"
      :modalContent="this.modalContent"
    />
  </div>
</template>

<script>
import { OpenVidu } from 'openvidu-browser'
import UserVideo from './video/UserVideo.vue'
import { onMounted } from 'vue'
import { mapActions, mapState } from 'vuex'
import { reportUser, enterMeeting, leaveMeeting } from '@/api/meeting'
import jwtDecode from 'jwt-decode'
import { WEBSOCKET_URL } from '@/config/index'
import SockJS from 'sockjs-client'
import Stomp from 'stompjs'
import MeetingChat from './MeetingChat.vue'
import ConfirmModal from '../ConfirmModal.vue'

// stompClient.debug = null // disable stomp loggings

const meetingStore = 'meetingStore'

export default {
  name: 'App',

  components: {
    UserVideo,
    MeetingChat,
    ConfirmModal
  },
  computed: {
    ...mapState(meetingStore, [
      'sessionId',
      'myPublisher',
      'opponentInfo',
      'isMatched'
    ])
  },
  data() {
    onMounted(() => {
      if (this.sessionId === '' || this.isMatched === false) {
        this.$router.push('/404')
        return
      }
      if (!this.myPublisher.publishAudio) this.isOn.audio = false
      if (!this.myPublisher.publishVideo) this.isOn.video = false

      const token = jwtDecode(sessionStorage.getItem('Authorization'))

      const myGender = token.gender
      if (myGender === 'M') {
        setTimeout(this.joinSession, 1000)
      } else {
        this.joinSession()
      }
      this.myNickname = token.NickName
      this.myImageNo = token.imageNo

      this.socket = new SockJS(WEBSOCKET_URL) // config !!!!!!!!
      this.stompClient = Stomp.over(this.socket)

      this.skip1 = setTimeout(() => {
        this.skipDisable = false
      }, 30000)
      this.stompClient.connect({}, () => {
        this.stompClient.subscribe(
          '/sub/chat/' + this.sessionId + 's',
          (msg) => {
            //     
            const response = JSON.parse(msg.body)
            const msgNickname = response.nickname
            if (
              response.content !== 'true' &&
              msgNickname !== this.myNickname
            ) {
              //   ...     
              let seconds = 5
              const msg = '   .'
              this.modalContent = seconds + '   .'
              const msgInterval = setInterval(() => {
                --seconds
                if (seconds === 0) clearInterval(msgInterval)
                this.modalContent = seconds + msg
              }, 1000)
              this.showModal = true
              this.autoExit = setTimeout(this.leaveSession, 5000)
              return
            }
            if (msgNickname === this.myNickname) {
              this.mySkip = true
            } else {
              this.yourSkip = true
            }

            if (this.mySkip && this.yourSkip) {
              this.level++
              this.mySkip = false
              this.yourSkip = false
              this.question_pick()
              if (this.level === 2) {
                this.skipDisable = true
                this.skip2 = setTimeout(() => {
                  this.skipDisable = false
                }, 30000)
                this.publisher.stream
                  .removeFilter()
                  .then(() => {})
                  .catch((error) => {
                    console.error(error)
                  })
              } else {
                this.skipDisable = true
              }
            }
          }
        )
      })
    })
    return {
      socket: null,
      stompClient: null,
      autoExit: null,
      question: null,
      camera: false,
      level: 1,
      skipDisable: true,
      myNickname: null,
      myImageNo: 0,
      skip1: null,
      skip2: null,
      //  ,    
      questions: [
        // [0]~[10]: 
        [
          '   .',
          ' e   .',
          '    .'
        ],
        [
          '     .',
          ' ()   .',
          '    .'
        ],
        [
          '    .',
          '    .',
          '   .'
        ],
        [
          '    .',
          '    .',
          '     .'
        ],
        [
          '    .',
          '   .',
          '   .'
        ],
        [
          '    .',
          '    .',
          '   .'
        ],
        [
          '    .',
          '   .',
          '    .'
        ],
        [
          '   .',
          '    .',
          '   .'
        ],
        [
          '   .',
          '   .',
          ' ,   .'
        ],
        [
          '  .',
          '    .',
          '    .'
        ],
        [
          '    .',
          '     .',
          '     .'
        ],
        // [11]~[21]: 
        ['  .   .'],
        ['  .   .'],
        ['  .  .'],
        ['  .'],
        ['    .'],
        ['    .'],
        ['  !'],
        ['   .'],
        ['   .'],
        ['  .  .'],
        ['  .  ?'],
        // [22]:  
        [
          '    .',
          '    .',
          '     .',
          '   ?',
          '    .',
          '    .'
        ]
        // mbti  ( ),   ?,   ?
        // 1 + MBTI0/1 + 1*1~3 + 1*1~3
        //   pop
      ],
      customQuestions: [],
      reportOptions: [
        { label: '  ', value: 0 },
        { label: ' ', value: 1 },
        { label: ' ', value: 2 },
        { label: '  ', value: 3 },
        { label: '', value: 4 },
        { label: '', value: 5 }
      ],
      reportNo: 0,
      category: null,
      content: '',
      popupMatching: false,
      popupReport: false,
      mySkip: false,
      yourSkip: false,

      OV: undefined,
      session: undefined,
      mainStreamManager: undefined,
      publisher: undefined,
      subscribers: [],
      isOn: {
        audio: true,
        video: true
      },
      token: undefined,
      showModal: false,
      modalContent: '',
      myUserName: Math.floor(Math.random() * 100)
      // myUserName:      (jwt )
      //  
    }
  },

  methods: {
    ...mapActions(meetingStore, ['setSessionId', 'setIsMatched']),
    question_pick() {
      const rand = Math.floor(Math.random() * this.customQuestions.length)
      const picked = this.customQuestions.pop(rand)
      this.question = picked
    },
    //   
    toggleAudio() {
      this.isOn.audio = !this.isOn.audio
      this.publisher.publishAudio(this.isOn.audio)
    },
    toggleVideo() {
      this.isOn.video = !this.isOn.video
      this.publisher.publishVideo(this.isOn.video)
    },

    joinSession() {
      // --- Get an OpenVidu object ---
      this.OV = new OpenVidu()

      // --- Init a session ---
      this.session = this.OV.initSession()
      // --- Specify the actions when events take place in the session ---

      // On every new Stream received...
      this.session.on('streamCreated', ({ stream }) => {
        const subscriber = this.session.subscribe(stream)
        this.subscribers.push(subscriber)
      })

      // On every Stream destroyed...
      this.session.on('streamDestroyed', ({ stream }) => {
        const index = this.subscribers.indexOf(stream.streamManager, 0)
        if (index >= 0) {
          this.subscribers.splice(index, 1)
        }
      })

      // On every asynchronous exception...
      this.session.on('exception', () => {})

      // --- Connect to the session with a valid user token ---

      // 'getToken' method is simulating what your server-side should do.
      // 'token' parameter should be retrieved and returned by your own backend
      this.getToken(this.sessionId).then((response) => {
        this.token = response.data.token
        this.session
          .connect(this.token, { clientData: this.myUserName })
          .then(() => {
            // --- Get your own camera stream with the desired properties ---

            const publisher = this.OV.initPublisher(undefined, {
              ...this.myPublisher,
              // audioSource: undefined, // The source of audio. If undefined default microphone
              // videoSource: undefined, // The source of video. If undefined default webcam
              // publishAudio: true, // Whether you want to start publishing with your audio unmuted or not
              // publishVideo: true, // Whether you want to start publishing with your video enabled or not
              // resolution: '640x480', // The resolution of your video
              // frameRate: 30, // The frame rate of your video
              // insertMode: 'APPEND', // How the video is inserted in the target element 'video-container'
              // mirror: false, // Whether to mirror your local video or not
              filter: {
                type: 'GStreamerFilter',
                options: {
                  command: 'pitch pitch=1.5'
                }
              }
            })

            this.mainStreamManager = publisher
            this.publisher = publisher
            // --- Publish your stream ---
            this.session.publish(this.publisher)
          })
          .catch((error) => {
            console.warn(
              'There was an error connecting to the session:',
              error.code,
              error.message
            )
          })
      })
      window.addEventListener('beforeunload', (e) => {
        e.preventDefault()

        this.setIsMatched(false)
        this.leaveSession()
      })
    },

    leaveSession() {
      const msg = { nickname: this.myNickname, content: false }
      this.stompClient.send(
        '/pub/chat/' + this.sessionId + 's',
        {},
        JSON.stringify(msg)
      )
      this.stompClient.disconnect()
      clearTimeout(this.autoExit)
      // --- Leave the session by calling 'disconnect' method over the Session object ---
      if (this.session) this.session.disconnect()

      this.session = undefined
      this.mainStreamManager = undefined
      this.publisher = undefined
      this.subscribers = []
      this.OV = undefined

      leaveMeeting(this.sessionId, this.token)

      window.removeEventListener('beforeunload', this.leaveSession)
      this.$router.push('/')
    },

    updateMainVideoStreamManager(stream) {
      if (this.mainStreamManager === stream) return
      this.mainStreamManager = stream
    },

    getToken(sessionId) {
      return this.createSession(sessionId)
    },
    // See https://docs.openvidu.io/en/stable/reference-docs/REST-API/#post-session
    createSession(sessionId) {
      return enterMeeting(sessionId)
    },
    levelUp() {
      if (this.skipDisable) {
        return
      }
      const msg = { nickname: this.myNickname, content: true }
      this.stompClient.send(
        '/pub/chat/' + this.sessionId + 's',
        {},
        JSON.stringify(msg)
      )
    },
    report() {
      const reportedNo = this.opponentInfo.no
      const category = this.category
      const content = this.content
      reportUser({ reportedNo, category, content })
    }
  },
  beforeMount() {
    //  push
    this.customQuestions.push(
      this.questions[22].at(
        Math.floor(Math.random() * this.questions[22].length)
      )
    )
    //  push
    this.opponentInfo.interests.forEach((index) => {
      const picked = this.questions[index].at(Math.floor(Math.random() * 3))
      this.customQuestions.push(picked)
    })
    //  push
    this.opponentInfo.personalities.forEach((index) => {
      Array.prototype.push.apply(
        this.customQuestions,
        this.questions[index + 11]
      )
    })
    this.question_pick()
  }
}
</script>

<style scoped>
.popup {
  background: #f3f1eb;
  width: 20%;
  min-width: 350px;
  max-width: 500px;
}
.popup-bar {
  background: #b3a286;
}
.popup-text {
  font-size: 150%;
}
.report-popup {
  background: #f3f1eb;
  width: 30%;
  min-width: 450px;
  max-width: 550px;
}
.video-box {
  width: 500px;
  height: 375px;
  overflow: hidden;
  border-radius: 10px;
}
.disabled {
  filter: grayscale(80%);
  cursor: wait;
}
.abled {
  cursor: pointer;
}
</style>
