async function requestModifyProfile(profile, success, fail) {
  const Authorization = 'Bearer ' + sessionStorage.getItem('Authorization')
  await api
    .put('/users/profile', JSON.stringify(profile), {
      headers: {
        Authorization: Authorization
      }
    })
    .then(success)
    .catch(fail)
}
async function requestProfile(success, fail) {
  const Authorization = 'Bearer ' + sessionStorage.getItem('Authorization')
  await api
    .get('/users/profile', {
      headers: {
        Authorization: Authorization
      }
    })
    .then(success)
    .catch(fail)
}

async function getEmail(success, fail) {
  const Authorization = 'Bearer ' + sessionStorage.getItem('Authorization')
  await api
    .get('/users/userInfo', {
      headers: {
        Authorization: Authorization
      }
    })
    .then(success)
    .catch(fail)
}

async function getBanuser(success, fail) {
  const Authorization = 'Bearer ' + sessionStorage.getItem('Authorization')
  await api
    .get('/ban', {
      headers: {
        Authorization: Authorization
      }
    })
    .then(success)
    .catch(fail)
}

async function deleteBanuser(no, success, fail) {
  const Authorization = 'Bearer ' + sessionStorage.getItem('Authorization')
  await api
    .delete(`/ban/${no}`, {
      headers: {
        Authorization: Authorization
      }
    })
    .then(success)
    .catch(fail)
}

async function addBanuser(bannedNo, success, fail) {
  const Authorization = 'Bearer ' + sessionStorage.getItem('Authorization')
  await api
    .post('/ban', JSON.stringify(bannedNo), {
      headers: {
        Authorization: Authorization
      }
    })
    .then(success)
    .catch(fail)
}

async function getmeetinglog(success, fail) {
  const Authorization = 'Bearer ' + sessionStorage.getItem('Authorization')
  await api
    .get('/meeting/recent-call', {
      headers: {
        Authorization: Authorization
      }
    })
    .then(success)
    .catch(fail)
}

export {
  trySignup,
  setConfirmKey,
  confirmEmailCode, //   
  checkEmailExist,
  checkNicknameExist, //   
  tryLogin,
  kakaoLogin,
  naverLogin,
  googleLogin,
  // 
  withdrawal, // 
  modifyPassword, //  
  modifyPasswordWithoutAuth,
  checkPassword, //  
  requestModifyProfile,
  requestProfile,
  getEmail,
  getBanuser, //   
  deleteBanuser, //  
  getmeetinglog,
  addBanuser
}
